<template>
  <div class="samples-edit">
    <el-card>
      <div slot="header">
        <span class="title">编辑变量</span>
      </div>
      <el-form label-width="100px">
        <el-form-item label="产品名称">
          <el-select v-model.trim="detail.rcProductName" filterable clearable placeholder="请选择产品名称">
            <el-option
              v-for="(item, index) in rcproductList"
              :key="index"
              :label="item.rcProductName"
              :value="item.rcProductName"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="模块类型">
          <el-select v-model.trim="detail.moduleType" placeholder="请选择模块类型">
            <el-option
              v-for="(item, index) in moduleTypeList"
              :key="index"
              :label="item.value"
              :value="item.key"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否使用">
          <el-switch v-model="detail.isuse" active-color="#409eff"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click.prevent.stop="updateSample">确定</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import {querySampleByName, updateSample, getAllModuletype} from '@/api/sample.js';

export default {
  name: 'samplesedit',
  data() {
    return {
      moduleName: '',
      moduleTypeList: [],
      detail: {
        rcProductName: '',
        moduleType: '',
        isuse: ''
      }
    };
  },
  computed: {
    rcproductList() {
      return this.$store.getters.rcproductList();
    }
  },
  created() {
    this.moduleName = this.$route.params.id;
    this.getAllModuletype();
    this.getSamplesDetail(this.moduleName);
  },
  methods: {
    getSamplesDetail(moduleName = '') {
      querySampleByName(moduleName)
        .then(result => {
          if (Array.isArray(result.result)) {
            const detail = result.result[0];
            this.detail.id = detail.id;
            this.detail.rcProductName = detail.productname || '';
            this.detail.moduleType = String(detail.moduletype);
            this.detail.isuse = detail.isuse;
            return;
          }
          this.detail = [];
        })
        .catch(err => console.log(err));
    },
    updateSample() {
      updateSample(this.detail.id, Number(this.detail.moduleType), this.detail.rcProductName, this.detail.isuse)
        .then(result => {
          this.$message.success('更新成功');
          this.$router.push('/samples');
        })
        .catch(err => console.log(err));
    },
    getAllModuletype() {
      getAllModuletype()
        .then(result => {
          const data = result.result;
          if (data) {
            this.moduleTypeList = Object.keys(data).map(key => {
              return {
                key,
                value: data[key]
              };
            });
          }
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style scoped>
.el-card {
  margin: 20px 15px;
}
</style>
